$header: 100;
$marco: 1280;

:root {
  --header: #{$header}px;
  --marco: #{$marco}px;
}

body {
  padding-top: #{$header}px;
  font-weight: 300;
  @include vars() {
    padding-top: var(--header);
  }
}

.marco {
  max-width: #{$marco}px;
  margin: auto;
  @include vars() {
    max-width: var(--marco);
  }
}

.contenido-interior {
  min-height: 53vh;
  &--licencias {
    @include break(md-mw) {
      min-height: auto;
    }
  }
}

.alerta-global {
  background: #FEAF49;
  padding: 3rem 2rem;
  text-align: center;
  color: blanco(1);
  font-size: 1.7rem;
  font-weight: 300;
  a {
    color: inherit;
  }
}

.contenido-dinamico {
  > :first-child {
    margin-top: 0;
    @include break(sm-mw) {
      margin-top: 30px;
    }
  }
}

.licencia-medica {
  &--rut {
    @include break(lg-mw) {
      min-height: 40vh;
    }
    @include break(md-mw) {
      min-height: auto;
    }
    @include break(sm-mw) {
      padding-top: 0;
      margin: 50px 0;
    }
  }
  &__form {
    @include break(md-mw) {
      margin-bottom: 20px;
    }
    > .form__grupo {
      margin-bottom: 0;
    }
  }
  &__interior {
    &__header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 15px;
      @include break(md-mw) {
        align-items: baseline;
        flex-direction: column-reverse;
      }
    }
  }
  &__destacado {
    color: $primario;
    font-weight: 400;
  }
  &__item {
    span {
      font-weight: 500;
      color: $primario;
      font-weight: 400;
    }
  }
  &__desc,
  &__fecha {
    margin-bottom: 15px;
    font-weight: 300;
    color: #666;
  }
  &__fecha {
    margin-bottom: 0;
    @include break(md-mw) {
      margin-bottom: 15px;
      width: 100%;
      text-align: right;
    }
    @include break(sm-mw) {
      text-align: left;
    }
  }
  &__tabla {
    margin-bottom: 0;
    table {
      border-collapse: separate;
    }
    thead {
      th {
        background: #F0F1F1;
        border: 1px solid #C7C8C8;
        color: #666;
        font-weight: 300;
        padding: 15px 10px;
      }
    }
    tbody {
      td {
        color: #666;
        font-weight: 300;
        padding: 15px 10px;
        text-align: center;
        &.td-monto {
          text-align: right;
          @include break(md-mw) {
            text-align: center;
          }
        }
        img {
          margin-bottom: 0;
        }
      }
    }
    &--lightbox {
      thead {
        th {
          &:last-child {
            text-align: center;
          }
        }
      }
      tbody {
        td {
          @include break(md-mw) {
            text-align: center;
          }
          &:first-child {
            text-align: left;
            @include break(md-mw) {
              text-align: center;
            }
          }
        }
      }
    }
  }
}

.lightboxs {
  &__contenedor {
    max-width: 645px;
    @include break(sm-mw) {
      max-width: 100%;
      padding: 25px;
    }
  }
  &__titulo {
    margin-top: 0;
    font-size: 3.8rem;
    color: $primario;
    font-weight: 300;
    @include break(md-mw) {
      font-size: 3rem;
    }
  }
}

.respuestas {
  &__item {
    padding: 5rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &--error {
      color: $error;
    }
  }
  &__icon {
    color: inherit;
    font-size: 4rem;
  }
  &__title {
    color: $primario;
    font-size: 3rem;
  }
  &__desc {
    margin-bottom: 3rem;
  }
}

.btn {
  &__grupo {
    .btn-licencia-medica {
      margin-top: 20px;
      margin-bottom: 0;
      padding: 1.5rem 3rem;
    }
  }
  &--volver {
    font-size: 1.6rem;
    font-weight: 300;
    color: $primario;
    text-decoration: none;
    position: relative;
    display: inline-flex;
    align-items: center;
    span {
      color: #E5EDF4;
      font-size: 3rem;
      position: relative;
      top: 3px;
    }
  }
  &--relevante {
    &:hover {
      background: #2e71c7;
      border-color: #2e71c7;
    }
  }
}

.form {
  &__grupo {
    &--help {
      font-size: 1.3rem;
      margin-top: 5px;
      color: #2c4e7a;
    }
  }
}

.login {
  &__form {
    .btn {
      &--relevante-borde {
        &:hover {
          // background: rgba(0, 59, 135, 0.2);
          background: #5779a5;
          border-color:#5779a5;
          color: #fff;
        }
      }
    }
  }
}

.arrow {
  position: relative;
  z-index: 11;
  margin-bottom: 10px;
  span {
    font-size: 2.6rem;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    // border: 2px solid $primario;
    background: #fff;
    color: $primario;
    display: block;
    margin: auto;
    animation: moveUp infinite 2s;
    &:before {
      position: relative;
      left: 2px;
      top: 2px;
    }
  }
}

.bloque {
  padding: 3rem;
  margin-bottom: 4rem;
  background: rgba($secundario,.05);
  > :last-child {
    margin-bottom: 0;
  }
}

.desplegable {
  margin-bottom: 40px;
  &__cabecera {
    &--aviso {
      display: block;
      font-size: 2em;
      padding: 20px;
      background: #f8f8f8;
      cursor: pointer;
      margin-bottom: 1px;
      background: #ebf4d4;
      color: #99c829;
      font-weight: 500;
      @include transicion;
      box-shadow: 0 0 0 rgba(20,40,84,0);
      border-radius: 0;
      outline: 0;
      margin-bottom: 0px;
    }
    .ui-icon {
      float: right;
      height: 30px;
      width: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      font-size: 25px;
      color: #fff;
      background: #99c88a;
      @include transicion;
      &.fln-abajo {
        animation:ayuda ease-in infinite 2s;
      }
      &:before {
        top: 1px;
        position: relative;
      }
    }
  }
}


@keyframes moveUp {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(10%);
  }

  0% {
    transform: translateY(0);
  }
}

@keyframes ayuda {
  40% {
    box-shadow: 0 0 0 0 rgba(153, 200, 138,0.4)
  }

  80% {
    box-shadow: 0 0 0 10px rgba(153, 200, 138,0.4)
  }

  100% {
    box-shadow: 0 0 0 10px rgba(153, 200, 138,0)
  }
}

