.menu-lateral {
  > ul {
    margin-bottom: 3rem;
    border: 1px solid rgba($primario, .1);
  }
  li {
    &:last-of-type {
      a {
        margin-bottom: 0;
      }
    }
  }
  a {
    display: block;
    text-decoration: none;
    font-size: 1.6rem;
    color: $primario;
    padding: 1rem 2rem;
    @include transicion;
    background: rgba($primario, .15);
    margin-bottom: 2px;
    &:hover {
      background: blanco(1);
    }
    &.activo {
      border-left: 6px solid $primario;
      background: blanco(1);
    }
    
  }
}