.datos {
  height: calc(100% - 30px);
  background: blanquear($primario, 90%);
  padding: 3rem;
  padding-bottom: 0;
  &__title {
    color: $primario;
    font-style: italic;
    font-size: 2.8rem;
    margin-top: 0;
    text-align: center;
  }
  &__items {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
  }
  &__item {
    width: 50%;
    max-width: 140px;
    margin: 0 auto 5rem;
    text-align: center;
    img {
      width: 40px;
      display: block;
      margin: auto;
      margin-bottom: 3rem;
    }
    &__title {
      color: $secundario;
      font-size: 1.5rem;
      font-weight: 300;
    }
  }
}