.header {
  @include posicion(fixed,0,0,null,0);
  background: $primario;
  height: #{$header}px;
  z-index: 99;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 2rem;
  @include vars() {
    height: var(--header);
  }
  &__item {
    // display: flex;
    &--logo {
      display: flex;
      align-items: center;
      text-decoration: none;
      img {
        display: block;
        width: 165px;
        margin-right: 3rem;
      }
    }
    &--user {
      @include break(xxl-mw) {
        margin-left: auto;
        margin-right: 0;
      }
    }
    &--burger {
      margin-left: 1.5rem;
      svg {
        fill: blanco(1);
      }
    }
  }
  &__text {
    font-size: 3.2rem;
    font-style: italic;
    font-weight: 600;
    color: blanco(1);
    // white-space: nowrap;
    span {
      font-weight: 300;
    }
    @media (max-width:1475px) {
      font-size: 2.4rem;
    }
  }
}