.bloque {
  padding: 3rem;
  margin-bottom: 4rem;
  color: $primario;
  background: rgba($secundario,.05);
  height: calc(100% - 4rem);
  display: flex;
  flex-direction: column;
  text-decoration: none;
  &--aviso {
    color: #99C829;
    background: #EBF4D4;
    h4, h6 {
      font-weight: 400;
    }
    ul {
      margin-left: 1rem;
      li::before {
        background: #99C829;
        border-color: #99C829;
      }
    }
  }
  h4, h6 {
    color: inherit;
  }
  p {
    span {
      color: #99C829;
      font-weight: 600;
    }
  }
  &__header {
    margin-bottom: 1rem;
    display: flex;
  }
  &__icon {
    margin-right: 1.5rem;
    svg {
      fill: $secundario;
    }
  }
  &__title {
    font-size: 2rem;
    font-weight: 400;
    color: gris(24);
    span {
      display: block;
      font-size: 2.4rem;
      color: $secundario;
    }
  }
  &__section {
    margin-bottom: 3rem;
  }
  &__item {
    margin-bottom: 2rem;
  }
  &__value {
    font-size: 2rem;
    font-weight: 400;
    color: gris(24);
  }
  &__label {
    font-size: 1.4rem;
    color: gris(24);
  }
  &__links{
    &__item {
      display: block;
      text-decoration: none;
      font-size: 1.6rem;
      color: $primario;
      padding: 1rem 2rem;
      @include transicion;
      background: rgba($primario, .15);
      margin-bottom: 2px;
      &:hover {
        background: blanco(1);
      }
    }
  }
  &__footer {
    margin-top: auto;
  }
  &__nota {
    font-size: 1.4rem;
    font-style: italic;
    color: gris(50);
    strong {
      font-weight: 400;
    }
  }
  &__subnota {
    color: blanco(1);
    padding: 1rem 3rem;
    background: #99C829;
    font-size: 1.4rem;
    margin: -3rem -3rem -3rem;
    position: relative;
    top: 3rem;
    @include break(md-mw) {
      padding: 1rem 1.5rem;
      margin: -1.5rem -1.5rem -1.5rem;
      top: 1.5rem;
    }
  }
  > :last-child {
    margin-bottom: 0;
  }
  @include break(md-mw) {
    padding: 1.5rem;
  }
}