.footer {
  background: rgba(ennegrecer($secundario,50%),.1);
  &__container {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: flex-end;
    padding: 0 1.5rem;
    @include break(md-mw) {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  }
  &__top {
    padding-top: 6rem;
    padding-bottom: 2.5rem;
    @include break(md-mw) {
      padding: 3rem 0;
    }
  }
  &__menu {
    &__title {
      color: $primario;
      text-transform: uppercase;
      font-size: 1.7rem;
      font-weight: 300;
    }
    &__link {
      display: inline-block;
      text-decoration: none;
      font-size: 1.4rem;
      line-height: 1;
      color: $primario;
      margin: 1rem 0;
      margin-right: 1rem;
      padding-right: 1rem;
      border-right: 1px solid rgba($primario,.6);
      @include transicion;
      &:last-of-type {
        border: 0;
      }
      &:hover {
        opacity: .6;
      }
      @include break(md-mw) {
        border: 0;
        display: block;
        padding: 1rem 0;
      }
    }
    @include break(md-mw) {
      margin-bottom: 3rem;
    }
  }
  &__fono {
    color: $primario;
    font-weight: 300;
    font-size: 1.7rem;
    span {
      font-size: 2.4rem;
      display: block;
      white-space: nowrap;
      font-weight: 500;
    }
    @include break(md-mw) {
      margin-bottom: 3rem;
    }
  }
  &__rrss {
    display: flex;
    &__red {
      margin-left: 1.5rem;
      @include transicion;
      svg {
        fill: $primario;
      }
      &:hover {
        opacity: .6;
      }
    }
  }
  &__bottom {
    background: $primario;
    padding: 2.5rem 0;
    @include break(md-mw) {
      padding: 3rem 0;
    }
  }
  &__logo {
    @include break(md-mw) {
      width: 110px;
      margin-bottom: 3rem;
      img {
        width: 100%;
      }
    }
  }
  &__textos {
    text-align: right;
    @include break(md-mw) {
      text-align: center;
    }
    p {
      color: blanco(1);
      font-size: 1.2rem;
      @include break(md-mw) {
        margin-bottom: 1.5rem;
      }
    }
  }
}