.banner {
  &-principal {
    overflow: hidden;
    position: relative;
    padding: 0 1.5rem;
    &__bg {
      @include posicion(absolute,0,0,0,0);
      background: negro(.7);
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: block;
        @include break(md-mw) {
          transform: scale(1.2);
          filter: blur(5px);
          opacity: .5;
        }
      }
    }
    &__container {
      position: relative;
      z-index: 1;
      width: #{$marco}px;
      margin: auto;
      padding: 130px 0;
      @include vars() {
        width: 100%;
        max-width: var(--marco);
      }
      @include break(md-mw) {
        padding: 5rem 0;
      }
    }
    &__title {
      color: blanco(1);
      font-style: italic;
      text-transform: uppercase;
      font-size: 4.5rem;
      span {
        display: block;
        font-weight: 300;
        text-transform: none;
      }
      @include break(md-mw) {
        font-size: 2.8rem;
      }
    }
  }
  &s{
    margin-bottom: 5rem;
  }
  position: relative;
  display: block;
  margin-bottom: 3rem;
  text-decoration: none;
  @include break(md-mw) {
    display: flex;
  }
  &__image {
    overflow: hidden;
    img {
      display: block;
      width: 100%;
      @include transicion;
    }
    @include break(md-mw) {
      flex: 1;
    }
  }
  &__data {
    @include posicion(absolute,null, 2rem, 2rem, null);
    width: 200px;
    height: 200px;
    padding: 6rem 1.5rem 1.5rem;
    @include break(md-mw) {
      @include posicion(relative,null, 0, 0, null);
      flex: 1;
      width: auto;
      height: auto;
      display: flex;
      align-items: center;
      padding: 2rem;
    }
  }
  &__title {
    color: blanco(1);
    margin: 0;
    font-size: 2.6rem;
    font-style: italic;
    font-weight: 300;
    text-transform: uppercase;
    span {
      font-weight: 300;
      display: block;
      font-size: 1.7rem;
      text-transform: none;
      @include break(md-mw) {
        font-size: 1.4rem;
      }
    }
    @include break(md-mw) {
      font-size: 1.8rem;
    }
  }
  &:hover {
    img {
      transform: scale(1.05,1.05);
    }
  }
}