.guia {
  padding: 4rem 1.5rem;
  &__link {
    font-size: 1.4rem;
    padding: 0 1rem;
    border-right: 1px solid rgba($secundario, .6);
    text-decoration: none;
    line-height: 1;
    color: $secundario;
    &:last-of-type {
      border: 0;
    }
    &--active {
      color: gris(24);
      pointer-events: none;
    }
  }
  @include break(sm-mw) {
    display:none;
  }
}