.login {
  position: relative;
  overflow: hidden;
  padding: 0 1.5rem;
  margin-bottom: 3rem;
  background: negro(.8);
  &__container{
    padding: 6rem 0;
    width: #{$marco}px;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    z-index: 1;
    @include vars(){
      width: 100%;
      max-width: var(--marco);
    }
    @include break(md-mw) {
      flex-direction: column;
      padding: 3rem 0;
    }
  }
  &__bg {
    @include posicion(absolute,0,0,0,0);
    opacity: .6;
    background: negro(.7);
    background: url('../../img/login.jpg') no-repeat;
    background-size: cover;
    background-attachment: fixed;
    @include break(md-mw) {
      transform: scale(1.2);
      filter: blur(5px);
      opacity: .5;
    }
      // img {
      //   width: 100%;
      //   height: 100%;
      //   object-fit: cover;
      //   display: block;
      //   @include break(md-mw) {
      //     transform: scale(1.2);
      //     filter: blur(5px);
      //     opacity: .5;
      //   }
      // }
  }
  &__title {
    color: blanco(1);
    font-size: 4.5rem;
    text-transform: uppercase;
    font-style: italic;
    span {
      display: block;
      text-transform: none;
      font-weight: 300;
    }
    @include break(md-mw) {
      font-size: 2.8rem;
      margin-bottom: 3rem;
    }
  }
  &__form {
    padding: 3rem;
    background: blanco(1);
    width: 100%;
    max-width: 400px;
    margin-bottom: 0;
  }
}