.menu {
  $t : &;
  display: flex;
  margin: auto;
  &__link {
    height: #{$header}px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: blanco(1);
    font-size: 1.7rem;
    padding: 1rem;
    font-weight: 300;
    @include transicion;
  }
  &__megamenu {
    @include posicion(absolute,100%,0,null,0);
    background: $terciario;
    width: #{$marco}px;
    margin: auto;
    padding: 5rem;
    opacity: 0;
    visibility: hidden;
    @include transicion;
    transition-delay: 200ms;
    display: flex;
    align-items: center;
    @include vars() {
      width: 100%;
      max-width: var(--marco);
    }
    &__icon {
      margin-right: 5rem;
      img {
        width: 140px;
        display: block;
      }
    }
    &__title {
      font-size: 2.8rem;
      color: blanco(1);
    }
    &__row {
      display: flex;
      margin: 0 -3rem;
    }
    &__col {
      padding: 0 3rem;
    }
    &__link {
      display: block;
      font-size: 1.7rem;
      color: blanco(1);
      text-decoration: none;
      padding: 1rem 10px;
      font-weight: 300;
      @include transicion;
      &:hover {
        background: #2e6ec1;
      }
    }
  }
  &__item {
    &:hover {
      #{$t}__link {
        background: $terciario;
      }
      #{$t}__megamenu {
        opacity: 1;
        visibility: visible;
      }
    }
  }
}