.menu-responsive {
  $t: &;
  @include posicion(fixed,0,0,0,0);
  z-index:98;
  visibility: hidden;
  display: flex;
  justify-content: flex-end;
  @include break(xxl) {
    display: none;
  }
  &__overlay {
    @include posicion(absolute,0,0,0,0);
    background: transparent;
  }
  &__container {
    width:100%;
    max-width:360px;
    background: blanco(1);
    margin-top:90px;
    transform: translateX(100%);
    @include transicion;
    @include vars() {
      margin-top: var(--header);
    }
    > ul {
      padding: 1.5rem 0;
    }
  }
  &--active {
    visibility: visible;
    #{$t}__container {
      transform: translateX(0);
    }
    #{$t}__overlay {
      background: negro(.7);
    }
  }
  li {
    a {
      display: flex;
      justify-content: space-between;
      padding: 1rem;
      font-size: 1.7rem;
      text-decoration: none;
      color: $primario;
      background: blanco(1);
      font-weight: 300;
      &:focus {
        outline: 0;
      }
      &.ui-accordion-header {
        flex-direction: row-reverse;
      }
    }
    .submenu--dos {
      a {
        color: blanco(1);
        background: $primario;
      }
    }
    .submenu--tres {
      a {
        color: blanco(1);
        background: ennegrecer($primario, 20%);
      }
    }
    a {
      &.ui-accordion-header-active,
      &.activo {
        background: $secundario;
        color: blanco(1);
      }
    }
  }
}