.acceso {
  &s {
    padding: 7rem 3rem;
    background: rgba($secundario,.05);
    margin-bottom: 3rem;
    .owl {
      &-carousel {
        margin: 0;
      }
      &-dots {
        position: relative;
        bottom: 0;
        top: 3rem;
        margin-top: 3rem;
      }
      &-dot {
        span {
          border-radius: 0;
          border: 1px solid gris(24);
        }
        &.active {
          span {
            background: gris(24);
          }
        }
      }
    }
  }
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  position: relative;
  &::after {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    @include posicion(absolute,0,0,0,0);
    background: rgba($primario,.05);
    transform: scale(0);
    border-radius: 1rem;
    @include transicion;
    z-index: .1;
  }
  &:hover {
    &::after {
      transform: scale(1, 1.5);
    }
  }
  &__ico {
    width: 7rem;
    display: block;
    margin-right: 3rem;
  }
  &__title {
    font-size: 2.4rem;
    color: $secundario;
    line-height: 1;
    span {
      display: block;
      color: gris(24);
      font-size: 2rem;
    }
  }
}