.header-user {
  $t: &;
  position: relative;
  height: #{$header}px;
  display: flex;
  align-items: center;
  @include vars() {
    height: var(--header);
  }
  &__times {
    color: blanco(1);
    font-size: 1.3rem;
    width: 100%;
    margin-bottom: 1.5rem;
  }
  &__info {
    text-align: right;
    color: blanco(1);
    display: flex;
    align-items: center;
    justify-content: flex-end;
    line-height: 1;
    padding: 1rem 0;
    cursor: default;
    &:hover {
      #{$t}__options {
        visibility: visible;
        opacity: 1;
      }
    }
  }
  &__hi {
    font-size: 1.8rem;
    font-style: italic;
    font-weight: 300;
    @include break(xl-mw) {
      display: none;
    }
  }
  &__name {
    font-size: 2.2rem;
    font-style: italic;
    @include break(xl-mw) {
      display: none;
    }
  }
  &__icon {
    font-size: 2.5rem;
    width: 3rem;
    text-align: center;
    margin-left: 1rem;
    &::before {
      vertical-align: middle;
      @include break(xl-mw) {
        display: none;
      }
    }
    svg {
      fill: blanco(1);
    }
    @include break(xl-mw) {
      width: 3.5rem;
      margin-left: 0;
    }
  }
  &__times {
    color: blanco(1);
    font-size: 1.3rem;
    width: 100%;
    margin-bottom: 1.5rem;
    min-width: 250px;
  }
  &__options {
    @include posicion(absolute, 100%, 0, null, null);
    background: $terciario;
    padding: 2rem 3rem;
    visibility: hidden;
    opacity: 0;
    @include transicion;
  }
  &__option {
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: left;
    color: blanco(1);
    text-decoration: none;
    font-size: 1.7rem;
    font-weight: 300;
    padding: 1rem 10px;
    @include transicion;
    svg {
      fill: blanco(1);
    }
    &:hover {
      // opacity: 0.6;
      background: #2e6ec1;
    }
  }
  &__ico {
    margin-left: 3rem;
  }
}
